body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#171717;
  color:#fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 .DetailsPage{
	 display: none;
 } 
 
 .cursor-outline{
    position: fixed;
  top: 0;
  left: 0;
  background-color: #fff;
  height:20px;
  width:20px;
  border-radius: 50%;
  pointer-events: none;
}



.load-container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	z-index: 10;
	pointer-events: none;
  }
   .load-screen{
	position: absolute;
	padding-top: 0px;
	padding-left: 0px;
	padding-right: 0px;
	background-color: #ccc;

  }
  .Headd {
	display: none;
	/* pointer-events: none; */
  }
  .load-screen.left{
	left:0;
	width: 0%;
	height: 100%;
  }
  .load-screen.right{
	  right:0;
	  width: 0%;
	  height: 100%;
  }
  .load-screen.bottom{
	  bottom:0;
	  width: 100%;
	  height: 0%;
  }
  .load-screen.top{
	  top:0;
	  bottom:0;
	  width: 100%;
	  height: 0%;
  }

  html{
	  font-size: 16px;
  }
  @media(max-width:640px){
	  html{
		  font-size: 10px;
	  }
  }